/* header */
header {
  position: relative;
}

header h1 a {
  @include hide-text('../../assets/images/logo-white.png');
  background-size: contain;
  background-repeat: no-repeat;
}

.material-symbols-outlined {
  font-variation-settings: 'FILL' 0, 'wght' 400, 'GRAD' 0, 'opsz' 48;
}

.dropdown {
  display: flex;
  font-size: 1.25rem;
  gap: 4rem;
  @include mobile {
    /*顯示*/
    position: absolute;
    left: 0;
    top: 100%;
    width: 100%;
    max-height: 0%;
    transition: all 0.8s;
    overflow: hidden;
    z-index: 120;
    /*顯示樣式*/
    gap: 0;
    background-color: $primary;
    flex-direction: column;
    text-align: center;
    &.active {
      max-height: 400%;
    }
  }
  li {
    @include mobile {
      border-top: 1px solid white;
    }
  }
  a {
    @include hover-underline(white);
    @include mobile {
      padding-block: 25px;
      @include hover-reset;
      @include hover-background($primary-dark);
    }
  }
}
